import axios from 'axios';
import { action, computed, decorate, flow, observable } from 'mobx';
import { Parser } from 'tsv';

class Store {
	public get filteredRows(): any {
		const filtered: any[] = [];
		const { query, rows } = this;
		rows.forEach(cols => {
			for (const r of cols) {
				if (r.toString().indexOf(query) > -1) {
					filtered.push(cols);
					break;
				}
			}
		});
		return filtered;
	}
	public heads: any = {};
	public rows: any = [];
	public query: string = '';

	public fetchGoogleSpreadSheets = flow(function*(this, urlOrKey: string) {
		try {
			const cacheBursting = `${urlOrKey}&_=${new Date().getTime()}`;
			const response: string = yield axios(cacheBursting).then(
				r => r.data
			);
			const TSV = new Parser('\t', { header: false });
			const rows: any[] = TSV.parse(response);
			this.heads = rows.shift();
			this.rows.clear();
			rows.forEach(row => {
				if (row.length > 1) {
					this.rows.push(row);
				}
			});
		} catch (e) {
			console.log(e);
		}
	});

	public updateQuery = e => {
		this.query = e.target.value;
	};
}

decorate(Store, {
	heads: observable,
	rows: observable,
	query: observable,
	filteredRows: computed,
	fetchGoogleSpreadSheets: action,
	updateQuery: action,
});

const store = new Store();
export default store;



