import { inject, observer } from 'mobx-react';
import * as React from 'react';
import Tr from './Tr';

import { Input, Table } from 'reactstrap';
interface IProps {
	store?: any;
}
class App extends React.Component<IProps> {
	public componentDidMount() {
		this.props.store.fetchGoogleSpreadSheets(
			'https://docs.google.com/spreadsheets/d/e/2PACX-1vQieVcaj_MrjjCUFNlLrSh-H3qVgrjtS5HI2LFazc-5c6B_An2oSnXbXra7l2NGir1Px-BYZglX0tus/pub?output=tsv'
		);
	}

	public render() {
		const { heads, filteredRows, query, updateQuery } = this.props.store;
		return (
			<div className="App">
				<header className="App-header">
					<h1 className="App-title">Searcher</h1>
					<Input value={query} onChange={updateQuery} />
				</header>
				<Table>
					<thead>
						<tr>
							<td>{heads[0]}</td>
							<td>{heads[1]}</td>
							<td>{heads[2]}</td>
						</tr>
					</thead>
					<tbody>
						{filteredRows.map((row, i) => (
							<Tr key={i} row={row} />
						))}
					</tbody>
				</Table>
			</div>
		);
	}
}

export default inject('store')(observer(App));



